import styled from "styled-components";
import React from "react";
import { Button, TextField } from "@mui/material";
import getFormValues from "../../utils/getFormValues";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import axios from "axios";
import buildRoute from "../../utils/build-route";
import { API_URL } from "../../const";
import { API_ROUTES } from "../../routes";

const Register = () => {
  const { executeRecaptcha } = useGoogleReCaptcha();
  const [ disableForm, setDisableForm ] = React.useState(false);

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async (event) => {
    event.preventDefault();
    setDisableForm(true);
    try {
      const token = await executeRecaptcha?.("register");
      const values = getFormValues(event);

      const resp = await axios.post(buildRoute(API_URL, API_ROUTES.register), { values }, { headers: { "grecaptcha-token": token } });
      console.log("Response:", resp);
    } catch(error) {
      console.error(error);
    } finally {
      setDisableForm(false);
    }
  };

  return <RegisterStyle>
    <div className='register-container'>
      <h2>Register</h2>
      <form className='register-form' onSubmit={handleSubmit}>
        <div className='fields'>
          <div className='row'>
            <TextField name='email' size="small" label="Email" required />
          </div>
          <div className='row'>
            <TextField name='password' size="small" label="Password" type="password" required />
          </div>
          <div className='row'>
            <TextField name='repeatPassword' size="small" label="Repeat password" type="password" required />
          </div>
        </div>
        <div className='buttons'>
          <Button type="submit" disabled={disableForm}>Register</Button>
        </div>
      </form>
    </div>
  </RegisterStyle>;
};

export default Register;

const RegisterStyle = styled.div`
	flex-shrink: 0;
	flex-grow: 1;
	padding: 20px 50px;
	display: flex;
	align-items: center;
	justify-content: center;

	.register-container {
		background-color: var(--c-p2);
		padding: 20px 30px;
		border-radius: 10px;
		width: 100%;
		max-width: 500px;

		.register-form {
			margin: 20px 0;

			.fields {
				.row {
					> .MuiTextField-root {
						width: 100%;
					}
					:not(:last-child) {
						margin-bottom: 15px;
					}
				}
			}

			.buttons {
				margin-top: 20px;
				display: flex;
				justify-content: flex-end;
			}
		}
	}
`;